<script>
import { mapGetters, mapState, mapMutations } from "vuex";
import ComponentMenu from "@/components/menu/ComponentMenu.vue";
import ComponentFooter from "@/components/footer/ComponentFooter.vue";
import PageConsultaClientesPedidos from '@/pages/consultaClientes/PageConsultaClientesPedidos.vue';
import PageConsultaClientesTitulos from '@/pages/consultaClientes/PageConsultaClientesTitulos.vue';
import PageConsultaClientesNotas from '@/pages/consultaClientes/PageConsultaClientesNotas.vue';

export default {
    name: "PageConsultaClientes",

    components: {
        ComponentFooter,
        PageConsultaClientesPedidos, 
        PageConsultaClientesTitulos, 
        PageConsultaClientesNotas,
        ComponentMenu
    },

    data: () => ({        
        filtroLocalCodigo: 0,
        filtroLocalNome: '',
        nomeTelaAba: '',
        botoesAbas:[
            { btnPedidos: false }, 
            { btnNotas: false },
            { btnTitulos: false },
            { btnObservacoes: false },
            { btnReferencias: false },
            { btnContatos: false },
            { btnPessoas: false }
        ], 

        mostrarPesquisa: false,

        intervalConsultaAbas: 0,        
    }),

    created() {
        this.atualizarAutomaticoClientes();

        // this.atualizarAutomaticoAbas();

        // this.recarregarDados();

        this.botoesAbas.forEach((element, index) => {
            this.botoesAbas[index] = false;
        });
    },
    
    //  beforeUnmount () {
    //     this.atualizarAutomaticoClientes();
    // },

    mounted() {
        let campoFiltroCodigo = document.querySelector('#filtroCodigoCliente');
        campoFiltroCodigo.focus(function() {
            this.select();
        })
        // let campoFiltroCodigo = document.querySelector('#filtroCodigoCliente');
        // campoFiltroCodigo.focus();
    },

    methods: {
        ...mapMutations(
            ["FILTRO_CODIGO_CLIENTE", 
             "FILTRO_NOME_CLIENTE",
             "FILTRO_NOME_CLIENTE_PESQUISA",
             "FILTRO_CODIGO_CLIENTE_PEDIDOS",
             "FILTRO_CODIGO_CLIENTE_TITULOS",
             "FILTRO_CODIGO_CLIENTE_NOTAS"]),        

        atualizarAutomaticoClientes()  {
            this.$store.dispatch("getFetchClientes", { self: this });                        
            this.pesquisarDados(99999999,'NNNNNNNNN');
        },

        recarregarDados()  {
            // console.log('this',this)
            // this.$store.dispatch("getFetchClientesPedidos", { self: this, fCodCliente });                                                            
            // this.carregarDadosPedidos(fCodCliente);

            // this.$store.dispatch("getFetchClientesTitulos", { self: this });                                                            
            // this.carregarDadosTitulos(fCodCliente);

            // this.$store.dispatch("getFetchClientesNotas", { self: this });                                                            
            // this.carregarDadosNotas(fCodCliente);
        },

        filterClientes() {

        },      
        
        pesquisarDados(fCodCliente, fNomeCliente) {
            let codigoCliente = fCodCliente;
            let nomeCliente = fNomeCliente;
            let quantidadeCaracteres = nomeCliente.length;
            // console.log('cod',fCodCliente, 'nome', fNomeCliente, 'qte',quantidadeCaracteres)

            this.mostrarPesquisa = false;

            if ( quantidadeCaracteres > 3 ) {
                if ((codigoCliente === 0 || codigoCliente === '' || codigoCliente === ' ') && 
                    ((nomeCliente ==='' || nomeCliente ===' ') || ((nomeCliente !=='' || nomeCliente !==' ') && 
                    (quantidadeCaracteres <= 3 )))) {
                    codigoCliente = 99999999;
                    nomeCliente = 'NNNNNNNNN'                
                    // if ( quantidadeCaracteres !== 0 && quantidadeCaracteres <= 3 ) {
                    //     alert("ATENÇÃO!! Informe pelo menos 4 caracteres no campo!")
                    // }
                } else {
                    this.filtroLocalNome = '';
                    this.FILTRO_CODIGO_CLIENTE(codigoCliente);
                    this.FILTRO_NOME_CLIENTE(nomeCliente);

                    this.carregarDadosPedidos(99999999);
                    this.carregarDadosTitulos(99999999);
                    this.carregarDadosNotas(99999999);
                    this.nomeTelaAba = '';
                }
            } else {
                if ( codigoCliente !== 0 && codigoCliente !== '' ) {
                    this.FILTRO_CODIGO_CLIENTE(codigoCliente);
                    this.FILTRO_NOME_CLIENTE(nomeCliente);

                    this.carregarDadosPedidos(99999999);
                    this.carregarDadosTitulos(99999999);
                    this.carregarDadosNotas(99999999);
                    this.nomeTelaAba = '';
                }
            }
        },

        pesquisarDados2(fCodCliente, fNomeCliente) {
            let codigoCliente = fCodCliente;
            let nomeCliente = fNomeCliente;
            let quantidadeCaracteres = nomeCliente.length;

            if ( quantidadeCaracteres > 3 && nomeCliente !== 'NNNNNNNNN' ) {
                this.mostrarPesquisa = true;
                
                if ((codigoCliente === 0 || codigoCliente === '' || codigoCliente === ' ') && 
                    ((nomeCliente ==='' || nomeCliente ===' ') || ((nomeCliente !=='' || nomeCliente !==' ') && 
                    (quantidadeCaracteres <= 3 )))) {
                    codigoCliente = 99999999;
                    nomeCliente = 'NNNNNNNNN'
                    // if ( quantidadeCaracteres !== 0 && quantidadeCaracteres <= 3 ) {
                    //     alert("ATENÇÃO!! Informe pelo menos 4 caracteres no campo!")
                    // }
                } else {
                    this.filtroLocalCodigo = '';
                    // this.FILTRO_CODIGO_CLIENTE(codigoCliente);
                    this.FILTRO_NOME_CLIENTE_PESQUISA(nomeCliente);

                    this.carregarDadosPedidos(99999999);
                    this.carregarDadosTitulos(99999999);
                    this.carregarDadosNotas(99999999);
                    this.nomeTelaAba = '';
                }
            } else {
                codigoCliente = 99999999;
                nomeCliente = 'NNNNNNNNN'
                this.FILTRO_NOME_CLIENTE_PESQUISA(nomeCliente);
            }
        },

        selecionarNomeCliente(codigoCliente, nomeCliente) {
            // console.log(nomeCliente);
            this.filtroLocalNome = nomeCliente;
            this.mostrarPesquisa = false;

            this.pesquisarDados(codigoCliente,nomeCliente);
        },

        verificarClassAtivo() {
            return "classDisplayTrue";
        },
        verificarClassInativo() {
            return "classDisplayFalse";
        },

        // atualizarAutomaticoAbas: function() {
        //     console.log('enrtrou carregar')

        //     if ( this.intervalConsultaAbas === false || this.intervalConsultaAbas === 0 || this.intervalConsultaAbas === undefined ) {
        //         clearInterval(this.intervalConsultaAbas);

        //         this.intervalConsultaAbas = setInterval(() => {
        //             console.log('carregar')
        //             this.recarregarDados();
        //         }, 50000);
        //         console.log('apos',this.intervalConsultaAbas)
        //     } else {
        //         clearInterval(this.intervalConsultaAbas);
        //     }        
        // },            

        carregarDadosPedidos(fCodCliente) {
            var codigoCliente = fCodCliente;
            var nomeCliente = '';

            if ((codigoCliente === 0 || codigoCliente === '' || codigoCliente === ' ')) {
                codigoCliente = 99999999;
                nomeCliente = 'NNNNNNNNN'                
            }
            this.FILTRO_CODIGO_CLIENTE_PEDIDOS(codigoCliente, nomeCliente);
        },

        carregarDadosTitulos(fCodCliente) {
            var codigoCliente = fCodCliente;
            var nomeCliente = '';

            if ((codigoCliente === 0 || codigoCliente === '' || codigoCliente === ' ')) {
                codigoCliente = 99999999;
                nomeCliente = 'NNNNNNNNN'                
            }
            this.FILTRO_CODIGO_CLIENTE_TITULOS(codigoCliente, nomeCliente);
        },

        carregarDadosNotas(fCodCliente) {
            var codigoCliente = fCodCliente;
            var nomeCliente = '';

            if ((codigoCliente === 0 || codigoCliente === '' || codigoCliente === ' ')) {
                codigoCliente = 99999999;
                nomeCliente = 'NNNNNNNNN'                
            }
            this.FILTRO_CODIGO_CLIENTE_NOTAS(codigoCliente, nomeCliente);
        },

        verificarMotivo210(codigoMotivo) {
            var className = 'classCampoPadrao';
            if ( codigoMotivo === 210) {
                className = 'classVermelhoDestacado';
            } else {
                className = 'classCampoPadrao';
            }
            return className;
        },

        verificarSituacao(situacao) {
            var className = 'classCampoPadrao';
            if ( situacao === 'I' ) {
                className = 'classVermelhoDestacado';
            } else {
                className = 'classCampoPadrao';
            }
            return className;
        },

        formatarDecimal(valorDecimal) {
            return valorDecimal.toLocaleString('pt-BR', { 
                style: 'decimal', 
                currency: 'BRL', 
                minimumFractionDigits: 2, 
                maximumFractionDigits: 2 
            });
        },

        abrirAba(abaClicada, clienteParaAba, nomeAba) {
            var abaAtualClicada = abaClicada;
            var codClienteClicado = clienteParaAba;
            this.nomeTelaAba = nomeAba;
            if ( nomeAba === 'TÍTULOS' ) {
                this.carregarDadosTitulos(codClienteClicado);
                this.$store.dispatch("getFetchClientesTitulos", { self: codClienteClicado });                                                            
            } else {
                if ( nomeAba === 'NOTAS FISCAIS' ) {
                    this.carregarDadosNotas(codClienteClicado);
                    this.$store.dispatch("getFetchClientesNotas", { self: codClienteClicado });                                                            
                } else {
                    if ( nomeAba === 'PEDIDOS' ) {
                        this.carregarDadosPedidos(codClienteClicado);
                        this.$store.dispatch("getFetchClientesPedidos", { self: codClienteClicado });                                                            
                        // this.carregarDadosPedidos(codClienteClicado);

                    }
                }
            }

            this.botoesAbas.forEach((element, index) => {
                this.botoesAbas[index] = false;
                if ( abaAtualClicada == index ) {
                    this.botoesAbas[index] = true;
                }
            });
        },

        verificarCodigoAba() {
            // console.log(todosClientes.codCliente);            
        },

