        //             matches++;
        //             return this.filtroLocalNome;
        //         }
        //     })
        // },         

    }, 

    computed: { 
       ...mapState(["dadosGeraisClientes"]),

       ...mapGetters(
            ["todosClientes",
            "clientesPeloNome", 
            "filtroCodCliente", 
            "filtroNomeCliente"]),            
