export function getByMotorista(list, keyword) {
    const search = keyword.trim().toLowerCase();
    if ( !search.length || search ==='' || search === ' ' ) {
        return list;
    } else {
        return list.filter(item => item.nomeMotorista.toLowerCase().indexOf(search) > -1);
    }
}

export function getByData(list, date) {    
    if ( typeof(date) === Object ) {
        date = new Date(date).toLocaleDateString('pt-br', {timeZone: 'UTC'});
    } else {
        if ( typeof(date) === 'string' ) {
            date = converteData(date);
            date = new Date(date).toLocaleDateString('pt-br', {timeZone: 'UTC'});
        }
    }

    if ( !date || date ==='' || date ===' '|| date === 'Invalid Date' ) {
        return list;
    } else {
        return list.filter(item => new Date(item.dataCarga).toLocaleDateString('pt-br', {timeZone: 'UTC'}) == date );
    }
}

export function getByCarga(list, number) {    
    number = parseInt(number);
    if ( !number || number === 0 ) {
        return list;
    } else {
        return list.filter(item => item.numCarga == number);
    }
}

export function getByNota(list, number) {    
    number = parseInt(number);
    if ( !number || number === 0 ) {
        return list;
    } else {
        return list.filter(item => item.notaFiscal == number);
    }
}

export function getByPedido(list, number) {    
    number = parseInt(number);
    if ( !number || number === 0 ) {
        return list;
    } else {
        return list.filter(item => item.numPedido == number);
    }
}

