function converteData(DataYYYYMMDD) {
    const dataSplit = DataYYYYMMDD.split("-");
    const novaData = new Date(parseInt(dataSplit[0], 10),
                  parseInt(dataSplit[1], 10) - 1,
                  parseInt(dataSplit[2], 10));
    return novaData;
}
