<template>
    <w-flex class="lg12 column cabecalhoGridNotas">        
        <w-flex class="lg12 row cabecalhoNotas">
            <label class="lg1 text-center">Filial</label>
            <label class="lg1 text-center">Cliente</label>
            <label class="lg2 text-center">Nota Fiscal</label>
            <label class="lg1 text-center">Série</label>
            <label class="lg2 text-center">Emissão</label>
            <label class="lg1 text-center">Trans.</label>
            <label class="lg3 text-left">Vendedor</label>
            <label class="lg2 text-right">Vlr. NF</label>
            <label class="lg2 text-center">Cond. Pgto.</label>
            <label class="lg5 text-left">Descr. Cond. Pgto. </label>
            <label class="lg3 text-left">Forma Pgto.</label>
            <label class="lg1 text-center">Situação</label>        
        </w-flex>

        <div class="lg12 gridNotas"  v-if="todasNotasClientes.length != 0">
            <w-flex class="lg12 row dadosNotas" v-for="(item, index) in todasNotasClientes" :key="index">
                <label class="lg1 text-center">{{ item.codFilial }}</label>
                <label class="lg1 text-center">{{ item.codCliente }}</label>
                <label class="lg2 text-center">{{ item.numNota }}</label>
                <label class="lg1 text-center">{{ item.codSerie }}</label>
                <label class="lg2 text-center">{{ new Date(item.dataEmissao).toLocaleDateString('pt-br', {timeZone: 'UTC'})}}</label>
                <label class="lg1 text-center">{{ item.tnsProduto }}</label>
                <label class="lg3 text-left">{{ item.nomeRepres }}</label>
                <label class="lg2 text-right">{{formatarDecimal(parseFloat(item.valorNota))}}</label>
                <label class="lg2 text-center">{{ item.codCondPgto }}</label>
                <label class="lg5 text-left">{{ item.descCondPgto }}</label>
                <label class="lg3 text-left">{{ item.descForma }}</label>
                <label class="lg1 text-center">{{ item.situacao }}</label>
            </w-flex>
        </div>
        <div v-else>
            <h2 style="text-align: center; padding-top: 2em">Nenhum Registro Encontrado</h2>
        </div>
    </w-flex>
</template>

<script>
import { mapGetters, mapState, mapMutations } from "vuex";

export default {
    name: 'PageConsultaClientesNotas',

    data: () => ({   

    }),

    created() {

    },
    
    mounted() {
        let campoFiltroCodigo = document.querySelector('#filtroCodigoCliente');
        campoFiltroCodigo.focus(function() {
            this.select();
        })
    },

    methods: {
        ...mapMutations(
            ["FILTRO_CODIGO_CLIENTE_NOTAS"]),        

        carregarNotasCliente()  {
            // this.$store.dispatch("getFetchClientesNotas", { self: this });                        
            // this.pesquisarDados(99999999);
        },

        filterClientes() {

        },      
        
        pesquisarDados(fCodCliente) {
            var codigoCliente = fCodCliente;
            var nomeCliente = '';

            if ((codigoCliente === 0 || codigoCliente === '' || codigoCliente === ' ')) {
                codigoCliente = 99999999;
                nomeCliente = 'NNNNNNNNN'
            }
            this.FILTRO_CODIGO_CLIENTE_NOTAS(codigoCliente, nomeCliente);
        },

        formatarDecimal(valorDecimal) {
            return valorDecimal.toLocaleString('pt-BR', { 
                style: 'decimal', 
                currency: 'BRL', 
                minimumFractionDigits: 2, 
                maximumFractionDigits: 2 
            });
        },

        verificarDiasVencimento(qtdDias) {
            var className = 'fonteVerde';
            if ( qtdDias < 0 ) {
                className = 'fonteVermelho';
            } else {
                if ( qtdDias == 0 ) {
                    className = 'fonteAmarelo';
                } else {
                    className = 'fonteVerde';
                }
            }
            return className;
        }
    }, 

    computed: { 
       ...mapState(["dadosClientesNotas"]),

       ...mapGetters(
            ["todasNotasClientes", 
            "filtroCodClienteNotas",
            ]), 
    },
};
</script>

<style>
template {
    position: absolute;
    overflow: scroll;
}

.cabecalhoGridNotas {
    overflow-y: scroll;
    height: 23em;
    position: absolute;
}

.cabecalhoNotas {
    position: fixed !important;
    overflow-y: scroll;
    background-color: rgb(129, 137, 151) !important;
    border: solid 1px black;
    font-size: 0.94em;
    height: 1.5em !important;
}

.cabecalhoNotas label {
    padding: 0px 5px;
    box-shadow: 0px 0px 1px rgb(231, 228, 228);
}
.dadosNotas {
    font-size: 0.93em;
    height: 1.35em !important;
}

.dadosNotas:nth-child(even) {
    background-color: #CCC;
}
.dadosNotas:nth-child(odd) {
    background-color: #FFF;
}
.dadosNotas:hover {
    background-color: black;
    color: white;
}

.fonteVermelho {
    color: red;
}
.fonteAmarelo {
    color: rgb(184, 184, 3);
}
.fonteVerde {
    color: green;
}
</style>