<template>
    <w-flex>
        <component-grid sizeGrid="xs4 md4 lg4">
            <w-flex>
                <component-grid sizeGrid="xs4 md4 lg4">
                    <w-button class="botaoMenu white pl8 pr8 pb3 pt3 box bdrs2" bg-color="blue-grey-dark5" @click="rotaHome()">
                        HOME
                    </w-button>
                </component-grid>

                <component-grid sizeGrid="xs4 md4 lg4">
                    <w-menu align-left v-model="showMenuCadastros" shadow custom>
                        <template #activator="{ on }">
                            <w-button class="botaoMenu white pl7 pr7 pb3 pt3 box bdrs2" bg-color="blue-grey-dark5" v-on="on">
                                CADASTROS
                            </w-button>
                        </template>

                        <w-list
                            bg-color="blue-grey-dark5"
                            color="white"
                            nav
                            :items="listCadastros"
                            item-class="px8"
                            @item-select="showMenuCadastros = false">
                        </w-list>
                    </w-menu>
                </component-grid>

                <component-grid sizeGrid="xs4 md4 lg4" v-if="verificarAcessoGrupo('DIRETORIA')">
                    <w-menu align-left v-model="showMenuDiretoria" shadow custom>
                        <template #activator="{ on }">
                            <w-button class="botaoMenu white pl8 pr8 pb3 pt3 box bdrs2" bg-color="blue-grey-dark5" v-on="on">
                                DIRETORIA
                            </w-button>
                        </template>

                        <w-list
                            bg-color="blue-grey-dark5"
                            color="white"
                            nav
                            :items="listDiretoria"
                            item-class="px8"
                            @item-select="showMenuDiretoria = false">
                        </w-list>
                    </w-menu>
                </component-grid>

                <component-grid sizeGrid="xs4 md4 lg4" v-if="verificarAcessoGrupo('EXPEDICAO')">
                    <w-menu  align-left v-model="showMenuExpedicao" shadow custom>
                        <template #activator="{ on }">
                            <w-button class="botaoMenu white pl8 pr8 pb3 pt3 box bdrs2" bg-color="blue-grey-dark5" v-on="on">
                                EXPEDIÇÃO
                            </w-button>
                        </template>

                        <w-list
                            bg-color="blue-grey-dark5"
                            color="white"
                            nav
                            :items="listExpedicao"
                            item-class="px8"
                            @item-select="showMenuExpedicao = false">
                        </w-list>
                    </w-menu>
                </component-grid>
            </w-flex>
        </component-grid>

        <component-grid sizeGrid="xs1 md1 lg6">
            <div>
                
            </div>
        </component-grid>

        <component-grid sizeGrid="xs1 md1 lg2">
            <div>
                <component-usuario-logado></component-usuario-logado>
            </div>
        </component-grid>
    </w-flex>
</template>

<script>
import ComponentGrid from "@/components/geral/ComponentGrid.vue";
import ComponentUsuarioLogado from "@/components/geral/ComponentUsuarioLogado.vue";
import { mapGetters } from "vuex";

export default {
    name: 'ComponentMenu',

    components: {
        ComponentGrid, ComponentUsuarioLogado
    },

    data() {
        return {
            showMenuCadastros: false,

            showMenuDiretoria: false,

            showMenuExpedicao: false,

            listCadastros: [
                { label: 'Consulta Clientes', route: '/consultaclientes' },
                // { label: 'Menu Teste 1', route: '#item2' },
                // { label: 'Menu Teste 2', route: '#item3' }
            ],

            listDiretoria: [
                { label: 'Análise Pedidos Aprovados', route: '/pedidosaprovados' },
                // { label: 'Menu Teste 1', route: '#item2' },
                // { label: 'Menu Teste 2', route: '#item3' }
            ],

            listExpedicao: [
                { label: 'Suporte Entregas', route: '/suporteentregas'},
                // { label: '...', route: '#item2' },
                // { label: '...', route: '#item3' }
            ],
        }
    },
    
    created() {

    },    

    methods: {
        rotaHome() {
            this.$router.push('/home');
        },

        rotaConsultaClientes() {
            this.$router.push('/consultaclientes');
        },

        rotaSuporteEntregas() {
            this.$router.push('/suporteentregas');
        },

        verificarAcessoGrupo(nomeMenu) {
            if ( nomeMenu === 'DIRETORIA' ) {
                if ( this.getGrupoUsuarioLogado === 'SENIOR' || this.getGrupoUsuarioLogado === 'DIRETORIA' ) {
                    return true;
                }
            } else {
                if ( nomeMenu === 'EXPEDICAO' ) {
                    if ( this.getGrupoUsuarioLogado === 'SENIOR' || this.getGrupoUsuarioLogado === 'DIRETORIA'  || this.getGrupoUsuarioLogado === 'EXPEDICAO' ) {
                        return true;
                    }
                }
            }
        }
    },

    computed: {
        ...mapGetters(["getGrupoUsuarioDiretoria","getGrupoUsuarioLogado"]),          
    },

    watch: {

    }    
}
</script>

<style scoped>
    .botaoMenu, .w-list__item  {
        border-bottom: solid 1px white !important;
        border-right: solid 1px white !important;
        font-size: 0.9em !important;
        min-width: 10em !important;
        font-weight: 400 !important;
    }
    .botaoMenu:hover, .botaoMenu:active, .w-list__item:hover, .w-list__item:active {
        background-color: var(--cor04) !important;
        border-bottom: solid 1px red !important;
        border-right: solid 1px red !important;
    }

    .w-menu--bottom {
        margin-top: 7px !important;
    }
</style>