export function getByNomeCliente(list, keyword) {
    const search = keyword.trim().toLowerCase();
    if ( !search.length || search ==='' || search === ' ' || search === undefined || list.length === 0 ) {
        return list;
    } else {
        if ( list.length !== 0 || list.length !== 'Proxy' ) {
            return list.filter(item => item.nomeCliente.toLowerCase().indexOf(search) > -1);
        }
    }
}

export function getByCodCliente(list, number) {    
    number = parseInt(number);
    if ( !number || number === 0 ) {
        return list;
    } else {
        return list.filter(item => item.codCliente == number);
    }
}