import {getByCodCliente,getByNomeCliente} from '@/store/filtrosPesquisas/filtrosConsultaClientes.js'

const urlApi = "http://servidor:3000/";
const urlOrigem = ["http://servidor:8090", "http://localhost:8080", "http://192.168.15.75:8080", "http://192.168.15.48:8080"];

// PADRÃO VIDEOS JULIUS
const moduloConsultaClientesPedidos = {
    state: {
        dadosClientesPedidos: [],

        filtroCodigoClientePedidos: 0,

        filtroNomeClientePedidos: ''
    },

    getters: {
        filtroCodigoClientePedidos(state) {
            return state.filtroCodigoClientePedidos;
        },

        filtroNomeClientePedidos(state) {
            return state.filtroNomeClientePedidos;
        },

        todosPedidosClientes(state) {
            return getByCodCliente(getByNomeCliente(
                state.dadosClientesPedidos,
                state.filtroNomeClientePedidos), 
                state.filtroCodigoClientePedidos);                
        }
    },

    actions: {        
        getFetchClientesPedidos: ({ commit }, { self }) => {
            let requestOptions = {
                origin: urlOrigem,
                optionSuccessStatus: 200
            };

            fetch(`${urlApi}consultaclientespedidos/${self}`, requestOptions).then((resposta) => {
                return resposta.json();
            }).then ((resposta) => {      
                commit('BUSCAR_DADOS_BD_PEDIDOS', resposta);
            })
        },
    },

    mutations: {
        BUSCAR_DADOS_BD_PEDIDOS: (state, dadosPedidos) => {
            state.dadosClientesPedidos = dadosPedidos;
        },

        FILTRO_CODIGO_CLIENTE_PEDIDOS: ( state, payload ) => {
            state.filtroCodigoClientePedidos = payload;
        },

        FILTRO_NOME_CLIENTE_PEDIDOS: ( state, payload ) => {
            state.filtroNomeClientePedidos = payload;
        }
    },
}

export default moduloConsultaClientesPedidos;