import { createRouter, createWebHistory } from 'vue-router';
import PageLogin from '@/pages/login/PageLogin.vue';  //TEM QUE SER A PAGINA INICIAL - FOI COMENTADO SOMENTE PARA FACILITAR
import PageHome from '@/pages/home/PageHome';
import PagePedidosAprovados from '@/pages/pedidosAprovados/PagePedidosAprovados';
import PageSuporteEntregas from '@/pages/suporteEntregas/PageSuporteEntregas';
// import PageConsultaClientes from '@/pages/consultaClientes/PageConsultaClientes';
import PageConsultaClientes from '@/pages/consultaClientes/PageConsultaClientes';

const routes = [
    {
      path: '/',
      name: 'PageLogin',
      component: PageLogin,
    },
    {
      path: '/home',
      name: 'PageHome',
      component: PageHome
    },
    {
      path: '/pedidosaprovados',
      name: 'PagePedidosAprovados',
      component: PagePedidosAprovados
    },
    {
      path: '/suporteentregas',
      name: 'PageSuporteEntregas',
      component: PageSuporteEntregas
    },
    {
      path: '/consultaclientes',
      name: 'PageConsultaClientes',
      component: PageConsultaClientes
    }

]

const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    routes
})

export default router;