<template>
    <w-flex class="lg12 column cabecalhoGridTitulos">        
        <w-flex class="lg12 row cabecalhoTitulos">
            <label class="lg1 text-center">Filial</label>
            <label class="lg2 text-center">Cliente</label>
            <label class="lg2 text-center">Nº Título</label>
            <label class="lg2 text-center">Tipo</label>
            <label class="lg2 text-center">Emissão</label>
            <label class="lg2 text-center">Vencimento</label>
            <label class="lg2 text-right">Vlr. Original</label>
            <label class="lg2 text-right">Vlr. Aberto</label>
            <label class="lg2 text-center">Qt. Dias</label>
            <label class="lg1 text-center">Sit.</label>
            <label class="lg2 text-center">Portador</label>
            <label class="lg4 text-left">Forma Pgto.</label>        
        </w-flex>

        <div class="lg12 gridTitulos" v-if="todosTitulosClientes.length != 0">
            <w-flex class="lg12 row dadosTitulos" v-for="(item, index) in todosTitulosClientes" :key="index">
                <label class="lg1 text-center">{{item.codFilial}}</label>
                <label class="lg2 text-center">{{item.codCliente}}</label>
                <label class="lg2 text-center">{{item.numTitulo}}</label>
                <label class="lg2 text-center">{{item.tipoTitulo}}</label>
                <label class="lg2 text-center">{{ new Date(item.dataEmissao).toLocaleDateString('pt-br', {timeZone: 'UTC'})}}</label>
                <label class="lg2 text-center">{{ new Date(item.vencimento).toLocaleDateString('pt-br', {timeZone: 'UTC'})}}</label>
                <label class="lg2 text-right">{{formatarDecimal(parseFloat(item.valorOriginal))}}</label>
                <label class="lg2 text-right">{{formatarDecimal(parseFloat(item.valorAberto))}}</label>
                <label class="lg2 text-center" :class="verificarDiasVencimento(item.diasVencimento)">{{item.diasVencimento}}</label>
                <label class="lg1 text-center" :class="item.valorAberto <= 0 ? 'classTitLiq' : 'classTitAbe'">{{item.situacao}}</label>
                <label class="lg2 text-center">{{item.codPortador}}</label>
                <label class="lg4 text-left">{{item.descForma}}</label>
            </w-flex>
        </div>
        <div v-else>
            <h2 style="text-align: center; padding-top: 2em">Nenhum Registro Encontrado</h2>
        </div>
    </w-flex>
</template>

<script>
import { mapGetters, mapState, mapMutations } from "vuex";

export default {
    name: 'PageConsultaClientesTitulos',

    data: () => ({   

    }),

    created() {

    },
    
    mounted() {
        let campoFiltroCodigo = document.querySelector('#filtroCodigoCliente');
        campoFiltroCodigo.focus(function() {
            this.select();
        })
    },

    methods: {
        ...mapMutations(
            ["FILTRO_CODIGO_CLIENTE_TITULOS"]),        

        carregarTitulosCliente()  {
            // this.$store.dispatch("getFetchClientesTitulos", { self: this });                        
            // this.pesquisarDados(99999999);
        },

        filterClientes() {

        },      
        
        pesquisarDados(fCodCliente) {
            var codigoCliente = fCodCliente;
            var nomeCliente = '';

            if ((codigoCliente === 0 || codigoCliente === '' || codigoCliente === ' ')) {
                codigoCliente = 99999999;
                nomeCliente = 'NNNNNNNNN'
            }
            this.FILTRO_CODIGO_CLIENTE_TITULOS(codigoCliente, nomeCliente);
        },

        formatarDecimal(valorDecimal) {
            return valorDecimal.toLocaleString('pt-BR', { 
                style: 'decimal', 
                currency: 'BRL', 
                minimumFractionDigits: 2, 
                maximumFractionDigits: 2 
            });
        },

        verificarDiasVencimento(qtdDias) {
            var className = 'fonteVerde';
            if ( qtdDias < 0 ) {
                className = 'fonteVermelho';
            } else {
                if ( qtdDias == 0 ) {
                    className = 'fonteAmarelo';
                } else {
                    className = 'fonteVerde';
                }
            }
            return className;
        }
    }, 

    computed: { 
       ...mapState(["dadosClientesTitulos"]),

       ...mapGetters(
            ["todosTitulosClientes", 
            "filtroCodClienteTitulos",
            ]), 
    },
};
</script>

<style>
template {
    position: absolute;
    overflow: scroll;
}

.cabecalhoGridTitulos {
    overflow-y: scroll;
    height: 23em;
    position: absolute;
}

.cabecalhoTitulos {
    position: fixed !important;
    overflow-y: scroll;
    background-color: rgb(129, 137, 151) !important;
    border: solid 1px black;
    font-size: 0.94em;
    height: 1.5em !important;
}
.cabecalhoTitulos label {
    padding: 0px 5px;
    box-shadow: 0px 0px 1px rgb(231, 228, 228);
}
.dadosTitulos {
    font-size: 0.93em;
    height: 1.35em !important;
}
.dadosTitulos label {
    padding: 0px 5px;   
    box-shadow: 0px 0px 1px rgb(145, 144, 144);
}

.dadosTitulos:nth-child(even) {
    background-color: #CCC;
}
.dadosTitulos:nth-child(odd) {
    background-color: #FFF;
}
.dadosTitulos:hover {
    background-color: black;
    color: white;
}

.fonteVermelho {
    color: red;
}
.fonteAmarelo {
    color: rgb(184, 184, 3);
}
.fonteVerde {
    color: green;
}
.classTitLiq {
    background-color: rgb(30, 199, 30);    
    box-shadow: inset 2px 1px 6px white !important;
    border: 1px solid white;
}
.classTitAbe {
    background-color: rgb(241, 59, 59);
    box-shadow: inset 2px 1px 6px white !important;
    border: 1px solid white;
}
</style>