<template>
    <w-app>
        <w-card shadow class="loginGeral info-dark3" bg-color="blue-grey-light4">
            <w-flex wrap class="column text-left">            
                <component-grid sizeGrid='lg12' paddingGrid='pa2' alignGrid='text-center'>
                    <img src="@/assets/logo.png">
                </component-grid>

                <component-grid sizeGrid='lg12' paddingGrid='pa1 pl8'>
                    <w-flex wrap class="text-left"> 
                        <component-grid sizeGrid='lg1' paddingGrid='pa1'>
                            <i class="fa-solid fa-user fa-lg"></i>
                        </component-grid>
                        <component-grid sizeGrid='lg10' paddingGrid='pa1'>
                            <w-input 
                                class="loginUsuario mb2" 
                                autocomplete="off"
                                autofocus 
                                label="Digite o usuário"         
                                @keyup.enter="focusCampoSenhaUsuario()"
                                v-model="loginUsuario">
                            </w-input>
                        </component-grid>
                    </w-flex>

                    <w-flex wrap class="text-left">
                        <component-grid sizeGrid='lg1' paddingGrid='pa1'>
                            <i class="fa-solid fa-lock fa-lg"></i>
                        </component-grid>
                        <component-grid sizeGrid='lg10' paddingGrid='pa1'>
                            <w-input 
                                ref="senhaUsuario"
                                class="mb2" 
                                id="senhaUsuario"
                                type="password"  
                                label="Digite a senha"
                                @keyup.enter="validarDadosLogin()"
                                v-model="senhaUsuario">
                            </w-input>
                        </component-grid>
                    </w-flex>
                </component-grid>

                <w-button
                    class="lg5 ma1 pa5"
                    bg-color="primary"
                    target="_blank" 
                    @click="validarDadosLogin()">
                        Acessar
                    <i class="fa-solid fa-arrow-right-to-bracket fa-lg pl2"></i>
                </w-button>                
            </w-flex>   

            <w-transition-fade y>
                <w-alert v-if="mostrarErroLogin" error plain>Usuário ou senha incorretos!</w-alert>
            </w-transition-fade>
        </w-card>
    </w-app>
</template>

<script>
import router from '@/router';
import ComponentGrid from '@/components/geral/ComponentGrid.vue';

export default {
    name: 'PaginaLogin',

    components: {
        ComponentGrid,
    },
        
    data() {
        return {      
            loginUsuario: '',
            senhaUsuario: '',
            nomeUsuarioStorage: false,
            mostrarErroLogin: false,
        }
    },        
        
    created() {
        var usuarioAuxiliar = sessionStorage.getItem('usuarioKauefer');
        if ( !usuarioAuxiliar ) {
            usuarioAuxiliar = localStorage.getItem('usuarioKauefer');
        }

        if ( usuarioAuxiliar ) {
            this.nomeUsuarioStorage = JSON.parse(usuarioAuxiliar);
            this.$router.push('/home')
        } else {
            this.$router.push('/')
        }

    },    

    mounted: function() {
        this.rotasMenu = router; 
        
        this.alturaTela = window.screen.height;
        this.larguraTela = window.screen.width;       
    },

    methods: {
        focusCampoSenhaUsuario: function() {
            let campoSenhaUsuario = document.querySelector('#senhaUsuario');
            campoSenhaUsuario.focus();
        },

        validarDadosLogin() {
            let urlApi = "http://servidor:3000/";            
            let urlOrigem = ["http://servidor:8090", "http://localhost:8080", "http://192.168.15.75:8080", "http://192.168.15.48:8080"];
            let requestOptions = {
                origin: urlOrigem,
                optionSuccessStatus: 200
            };


            fetch(`${urlApi}usuarioacessoapp/'${this.loginUsuario}'/'${this.senhaUsuario}'`, requestOptions).then((resposta) => {
                return resposta.json();
            }).then ((resposta) => {                                
                if ( resposta.length == '1' || resposta.length == 1 ) {
                    this.gravarDadosUsuarioStorage(resposta[0]);
                    this.$router.push('/home')
                } else {
                    this.mostrarErroLogin = 'true';
                }
            })
            .catch(error =>  {
                console.log(error);
                if ( this.loginUsuario ) {
                    this.gravarDadosUsuarioStorage();
                    this.$router.push('/home')                
                } else {
                    this.mostrarErroLogin = 'true';
                }
            })
        },
        

        gravarDadosUsuarioStorage(dadosUser) {
            this.$store.dispatch("getFetchUsuario", { self: dadosUser });                        

            const usuarioLogadoStorage = {
                usuarioStorage: this.loginUsuario,
                grupoStorage: dadosUser.NUMCAD,
                tokenStorage: Math.random().toString(36).substr(2) + Math.random().toString(36).substr(2),
            }
            sessionStorage.setItem('usuarioKauefer', JSON.stringify(usuarioLogadoStorage));                
            // if ( this.loginUsuario === 'adriano' || this.loginUsuario === 'Adriano' ) {
            //     localStorage.setItem('usuarioKauefer', JSON.stringify(usuarioLogadoStorage));                            
            // } else {
            //     sessionStorage.setItem('usuarioKauefer', JSON.stringify(usuarioLogadoStorage));                
            // }
        }
    },

    watch: {
        mostrarErroLogin() {
            setTimeout(() => {
                this.mostrarErroLogin = false;
            }, 3000);
        },

        larguraTela() {
            if ( this.larguraTela < 576 ) {
                this.sizeGrid = 'xs12';
            }
        }
    },

}
</script>

<style scoped>
.w-app {
    background-color: var(--cor01);
}

img {
    width: 60%;
    height: 60%;
}

.loginGeral {
    max-width: 35em;
    max-height: 35em;
    transform: translate(-50%, -50%);
    position: absolute;
    left: 50%;
    top: 50%;
}
</style>