import {getByCodCliente,getByNomeCliente} from '@/store/filtrosPesquisas/filtrosConsultaClientes.js'

const urlApi = "http://servidor:3000/";
const urlOrigem = ["http://servidor:8090", "http://localhost:8080", "http://192.168.15.75:8080", "http://192.168.15.48:8080"];

// PADRÃO VIDEOS JULIUS
const moduloConsultaClientesNotas = {
    state: {
        dadosClientesNotas: [],

        filtroCodigoClienteNotas: 0,

        filtroNomeClienteNotas: ''
    },

    getters: {
        filtroCodigoClienteNotas(state) {
            return state.filtroCodigoClienteNotas;
        },

        filtroNomeClienteNotas(state) {
            return state.filtroNomeClienteNotas;
        },

        todasNotasClientes(state) {
            return getByCodCliente(getByNomeCliente(
                state.dadosClientesNotas,
                state.filtroNomeClienteNotas), 
                state.filtroCodigoClienteNotas);                
        }
    },

    actions: {        
        getFetchClientesNotas: ({ commit }, { self }) => {
            let requestOptions = {
                origin: urlOrigem,
                optionSuccessStatus: 200
            };

            fetch(`${urlApi}consultaclientesnotas/${self}`, requestOptions).then((resposta) => {
                return resposta.json();
            }).then ((resposta) => {                
                commit('BUSCAR_DADOS_BD_NOTAS', resposta);
                // self.filterClientes();
            })
        },
    },

    mutations: {
        BUSCAR_DADOS_BD_NOTAS: (state, dadosNotas) => {
            state.dadosClientesNotas = dadosNotas;
        },

        FILTRO_CODIGO_CLIENTE_NOTAS: ( state, payload ) => {
            state.filtroCodigoClienteNotas = payload;
        },

        FILTRO_NOME_CLIENTE_NOTAS: ( state, payload ) => {
            state.filtroNomeClienteNotas = payload;
        }
    },
}

export default moduloConsultaClientesNotas;