<template>
    <w-app row>
        <div class="topoFixo">
            <aside>
                <!-- <w-flex row wrap class="text-left">   -->
                    <component-menu>
                        <!-- <w-flex row>
                            <span class="lg11 text-center align-self-center">
                                Usuário: {{nomeUsuarioStorage}}
                            </span>
                            <w-tooltip top style="cursor:pointer" @click="sairSistema()">
                                <template #activator="{ on }">
                                <w-icon
                                    class="fa-solid fa-power-off"
                                    v-on="on"
                                    lg
                                    color="red">
                                    mdi mdi-home
                                </w-icon>
                                </template>
                                Sair
                            </w-tooltip>
                        </w-flex> -->
                    </component-menu>
                <!-- </w-flex> -->
            </aside>
                <header>
                    <h4>Bem-vindo</h4>
                </header>
        </div>
        <main class="grow"></main>
                
        <component-footer></component-footer>
    </w-app>
</template>


<script>
import ComponentFooter from "@/components/footer/ComponentFooter.vue"
// import MenuPrincipal from '@/components/MenuPrincipal.vue';
import ComponentMenu from "@/components/menu/ComponentMenu.vue";

export default {
    name: "PageHome",

    components: {
      ComponentFooter, ComponentMenu
    },    

    data: () => ({
        nomeUsuarioStorage: false,
    }),

    created() {
        var usuarioAuxiliar = sessionStorage.getItem('usuarioKauefer');
        if ( !usuarioAuxiliar ) {
            usuarioAuxiliar = localStorage.getItem('usuarioKauefer');
        }

        if ( usuarioAuxiliar ) {
            usuarioAuxiliar = JSON.parse(usuarioAuxiliar);
            for ( var key in usuarioAuxiliar ) {
                if (Object.hasOwnProperty.call(usuarioAuxiliar, key)) {
                    if ( key === "usuarioStorage" ) {
                        this.nomeUsuarioStorage = usuarioAuxiliar[key];
                        if ( this.nomeUsuarioStorage.toUpperCase() === 'KAUEE') {
                            this.nomeUsuarioStorage = 'KAUE';
                        }                        
                    }
                } 
            }
            this.$router.push('/home')
        } else {
            this.$router.push('/')
        }
    },    


    methods: {
        sairSistema() {
            sessionStorage.clear();
            localStorage.clear();
            this.$router.push('/')
        },
    }
};
</script>

<style scoped>
* {
    margin: 0;
    padding: 0;
}

.w-app {
    /* padding: 1px; */
    background-color: #a1a1a1;
}

.topoFixo {
    position: sticky;
    top: 0;
    background: black;
    z-index: 100;
}

header, main {
    margin: 1px;
    padding: 8px;
    text-transform: uppercase;
    color: rgb(255, 255, 255);
    border: 1px solid rgba(0, 0, 0, 0.07);
}
aside {
    display: flex;
    justify-content: space-around;
    background-color: var(--cor01);
    margin: 1px;
    padding: 0.1em;
    text-transform: uppercase;
    color: white;
    border: 1px solid rgba(0, 0, 0, 0.07);
    /* z-index: 1000; */
}

.menu input{
    background-color: var(--cor01);
    color: white;
    display: inline;
    padding: 4px 50px 4px 50px;
    min-width: 20px;
    min-height: 30px;
    border-radius: 10px;
}

.title {
    padding: 4px 40px 4px 40px;
    background-color: black;
    box-shadow: 10px 8px 25px black;
    border-radius: 10px;
}

header {
    background-color: var(--cor02);
    min-height: 60px;
    position: sticky;
    top: 0;

    display: flex;
    justify-content:space-around;
    text-align: center;
    align-items: center;
    /* padding: 4px 40px 4px 40px;     */
}

main {
    display: flex;
    background-color: var(--cor03);
    background-image: url('../../assets/imagem-fundo.jpg');
    background-size: cover;
}

w-flex {
    display: flex;
}
</style>
