<template>
    <w-flex row>
        <component-grid sizeGrid="lg9 align-self-center text-right">
            Usuário: {{getNomeUsuarioLogado}}
        </component-grid>

        <component-grid sizeGrid="lg3 align-self-center text-center">
            <w-tooltip top style="cursor:pointer" @click="sairSistema()">
                <template #activator="{ on }">
                <w-icon
                    class="fa-solid fa-power-off"
                    v-on="on"
                    lg
                    color="red">
                    mdi mdi-home
                </w-icon>
                </template>
                Sair
            </w-tooltip>
        </component-grid>
    </w-flex>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex';
import ComponentGrid from '@/components/geral/ComponentGrid.vue';

export default {
    name: "ComponentUsuarioLogado",

    components: { ComponentGrid },

    created() {
        var usuarioAuxiliar = sessionStorage.getItem('usuarioKauefer');
        if ( !usuarioAuxiliar ) {
            usuarioAuxiliar = localStorage.getItem('usuarioKauefer');
        }

        if ( usuarioAuxiliar ) {
            usuarioAuxiliar = JSON.parse(usuarioAuxiliar);
            for ( var key in usuarioAuxiliar ) {
                if (Object.hasOwnProperty.call(usuarioAuxiliar, key)) {
                    if ( key === "usuarioStorage" ) {    
                        this.VERIFICAR_USUARIO_LOGADO(usuarioAuxiliar[key]);
                    } else {
                        if ( key === "grupoStorage" ) {    
                            this.VERIFICAR_GRUPO_USUARIO_LOGADO(usuarioAuxiliar[key]);
                        }
                    }
                } 
            }
        } else {
            this.$router.push('/')
        }
    },

    computed: { 
        ...mapGetters(["getNomeUsuarioLogado","getGrupoUsuarioLogado"]),          
    },

    methods: {
        ...mapMutations(
            [ "VERIFICAR_USUARIO_LOGADO","VERIFICAR_GRUPO_USUARIO_LOGADO" ]
        ),

        sairSistema() {
            sessionStorage.clear();
            localStorage.clear();
            this.VERIFICAR_USUARIO_LOGADO('false');
            this.$router.push('/')
        },
    }
}
</script>

<style scoped>
.w-flex {
    border-left: solid 0.2em white;
    font-size: 0.65em;
}
</style>>
