const urlApi = "http://SERVIDOR:3000/";
const urlOrigem = ["http://localhost:8080", "http://192.168.15.75:8080", "http://192.168.15.48:8080"];

// PADRÃO VIDEOS JULIUS
const moduloPedidosAprovados = {
    state: {
        return: {
            pedidos: [],
            quantidadePedidos: 0,
        }
    },

    getters: {
        pedidosAnalisar(state) {
            if ( state.pedidos ) {
                return state.pedidos.filter(( pedidos ) => {
                    return pedidos.pedidoAnalisado === '' || pedidos.pedidoAnalisado === 'N' || pedidos.pedidoAnalisado === null;
                })
            }
        },
        pedidosAnalisados(state) {
            if ( state.pedidos ) {                
                return state.pedidos.filter(( pedidos ) => {
                    return pedidos.pedidoAnalisado === 'S';
                })
            }
        },        
        retornarTotalPedidos(state) {
            return state.quantidadePedidos;
        }
    },

    actions: {        
        getFetchPedidos: ({ commit }, { self }) => {
            let count = 0;
            let requestOptions = {
                origin: urlOrigem,
                optionSuccessStatus: 200
            };

            fetch(`${urlApi}pedidosaprovados`, requestOptions).then((resposta) => {
                return resposta.json();
            }).then ((resposta) => {                
                count = resposta[0].quantidadeAnalisar;

                commit('BUSCAR_DADOS_BD', resposta);
                commit('QUANTIDADE_PEDIDOS_ANALISAR', count);                
                self.filterPedidos();
            })
        },

        updateFetchPedidos: ({ commit }, pedidoAlterar) => {
            let requestOptions = {
                method: 'PATCH',
                origin: urlOrigem,
                optionSuccessStatus: 200,
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ pedidoAlterar }),
            };

            fetch(`${urlApi}updpedidosaprovados`, requestOptions ).then((resposta) => {
                return resposta.text();
            }).then ((resposta) => {
                commit('ALTERAR_SITUACAO_PEDIDO', pedidoAlterar, resposta); 
            })
        },
    },

    mutations: {
        BUSCAR_DADOS_BD: (state, pedidos) => {
            state.pedidos = pedidos;
        },
    
        ALTERAR_SITUACAO_PEDIDO: (state, pedidoAlterar) => {
            state.pedidos.forEach(element => {
                if ( element.numeroPedido === pedidoAlterar[0].numeroPedido ) {
                    if ( element.pedidoAnalisado === 'S' ) {
                        element.pedidoAnalisado = 'N';
                        state.quantidadePedidos++;    
                    } else {
                        element.pedidoAnalisado = 'S';
                        state.quantidadePedidos--;    
                    }
                }
            });
        },

        QUANTIDADE_PEDIDOS_ANALISAR: (state, quantidade) => {
            state.quantidadePedidos = quantidade;
        }
    }
}

export default moduloPedidosAprovados;