import {getByCodCliente,getByNomeCliente} from '@/store/filtrosPesquisas/filtrosConsultaClientes.js'

const urlApi = "http://servidor:3000/";
const urlOrigem = ["http://servidor:8090", "http://localhost:8080", "http://192.168.15.75:8080", "http://192.168.15.48:8080"];

// PADRÃO VIDEOS JULIUS
const moduloConsultaClientesTitulos = {
    state: {
        dadosClientesTitulos: [],

        filtroCodigoClienteTitulos: 0,

        filtroNomeClienteTitulos: ''
    },

    getters: {
        filtroCodigoClienteTitulos(state) {
            return state.filtroCodigoClienteTitulos;
        },

        filtroNomeClienteTitulos(state) {
            return state.filtroNomeClienteTitulos;
        },

        todosTitulosClientes(state) {
            return getByCodCliente(getByNomeCliente(
                state.dadosClientesTitulos,
                state.filtroNomeClienteTitulos), 
                state.filtroCodigoClienteTitulos);                
        }
    },

    actions: {        
        getFetchClientesTitulos: ({ commit }, { self }) => {
            let requestOptions = {
                origin: urlOrigem,
                optionSuccessStatus: 200
            };

            fetch(`${urlApi}consultaclientestitulos/${self}`, requestOptions).then((resposta) => {
                return resposta.json();
            }).then ((resposta) => {                
                // console.log('titulo',resposta)
                commit('BUSCAR_DADOS_BD_TITULOS', resposta);
                // self.filterClientes();
            })
        },
    },

    mutations: {
        BUSCAR_DADOS_BD_TITULOS: (state, dadosTitulos) => {
            state.dadosClientesTitulos = dadosTitulos;
        },

        FILTRO_CODIGO_CLIENTE_TITULOS: ( state, payload ) => {
            state.filtroCodigoClienteTitulos = payload;
        },

        FILTRO_NOME_CLIENTE_TITULOS: ( state, payload ) => {
            state.filtroNomeClienteTitulos = payload;
        }
    },
}

export default moduloConsultaClientesTitulos;