import {getByCodCliente, getByNomeCliente} from '@/store/filtrosPesquisas/filtrosConsultaClientes.js'

const urlApi = "http://servidor:3000/";
const urlOrigem = ["http://servidor:8090", "http://localhost:8080", "http://192.168.15.75:8080", "http://192.168.15.48:8080"];

// PADRÃO VIDEOS JULIUS
const moduloConsultaClientes = {
    state: {
        dadosGeraisClientes: [],

        filtroCodigoCliente: 0,

        filtroNomeCliente: '',

        filtroNomeClientePesquisa: '',

        // filtros: [
        //     {
        //         dataInicial: '',
        //         dataFinal: '',
        //         cargaInicial: 0,
        //         cargaFinal: 0
        //     }
        // ],
    },

    getters: {
        filtroCodigoCliente(state) {
            return state.filtroCodigoCliente;
        },
        filtroNomeCliente(state) {
            return state.filtroNomeCliente;
        },

        filtroNomeClientePesquisa(state) {
            return state.filtroNomeClientePesquisa;
        },

        todosClientes(state) {
            return getByCodCliente(getByNomeCliente(
                state.dadosGeraisClientes,
                state.filtroNomeCliente), 
                state.filtroCodigoCliente);                
        },

        clientesPeloNome(state) {
            return getByCodCliente(getByNomeCliente(
                state.dadosGeraisClientes,
                state.filtroNomeClientePesquisa));                
        }        
    },

    actions: {        
        getFetchClientes: ({ commit }, { self }) => {
            let requestOptions = {
                origin: urlOrigem,
                optionSuccessStatus: 200
            };

            fetch(`${urlApi}consultaclientes`, requestOptions).then((resposta) => {
                return resposta.json();
            }).then ((resposta) => {                
                // console.log('Clientes',resposta)
                commit('BUSCAR_DADOS_BD', resposta);
                console.log(self);
                // self.filterClientes();
            })
        },
    },

    mutations: {
        BUSCAR_DADOS_BD: (state, dadosClientes) => {
            state.dadosGeraisClientes = dadosClientes;
        },

        FILTRO_CODIGO_CLIENTE: ( state, payload ) => {
            state.filtroCodigoCliente = payload;
        },

        FILTRO_NOME_CLIENTE: ( state, payload ) => {
            state.filtroNomeCliente = payload;
        },

        FILTRO_NOME_CLIENTE_PESQUISA: ( state, payload ) => {
            state.filtroNomeClientePesquisa = payload;
        }

    },
}

export default moduloConsultaClientes;