        atribuirSitEntrega(empresa, carga, seqRegistro, horaInicial, horaFinal, sitEntrega) {
            console.log('ini',horaInicial, 'fin', horaFinal, sitEntrega);
            var sitEntregaNova;
            // PENDENTE
            if ( horaInicial === '' || horaInicial === '00:00' || horaInicial === null ||
                 horaFinal === '' || horaFinal === '00:00' || horaFinal === null ) {
                sitEntregaNova = 2;
            }
            // EM ROTA
            if (( horaInicial !== '' && horaInicial !== '00:00' && horaInicial !== null ) &&
                ( horaFinal === '' || horaFinal === '00:00' || horaFinal === null )) {
                sitEntregaNova = 1;
            }
            // ENTREGUE
            if ( horaInicial !== '' && horaInicial !== '00:00' && horaInicial !== null && 
                 horaFinal !== '' && horaFinal !== '00:00' && horaFinal !== null ) {
                sitEntregaNova = 3;
            }

            var notaAlterarSituacao = [
                { "codEmpresa": empresa,
                  "numCarga": carga,
                  "seqRegistro": seqRegistro,
                  "sitEntrega": sitEntregaNova,
                }
            ]

            this.ALTERAR_SOMENTE_SITUACAO(notaAlterarSituacao);
        },


        alterarRegistros( empresa, carga, seqRegistro, ordemEntrega, 
                          ordemReal, horaInicial, horaFinal, sitPagamento, sitEntrega, obsEntrega, index) {
            let vaNomeRadio = index;
            let indReceberSel = document.getElementsByName(vaNomeRadio);
            // LOOP PARA VER O RADIO QUE ESTÁ CHECADO
            var indReceber = '';
            for (var i = 0; i < indReceberSel.length; i++) {
                if (indReceberSel[i].checked) {
                    indReceber = indReceberSel[i].value;
                }                
            }

            if ( obsEntrega !== '' && obsEntrega !== ' ' && obsEntrega !== null && obsEntrega !== undefined  ) {    
                // console.log(obsEntrega)
                obsEntrega = obsEntrega.toUpperCase();
                // obsEntrega = obsEntrega;
            }

            var notaAlterar = [
                { "codEmpresa": empresa,
                  "numCarga": carga,
                  "seqRegistro": seqRegistro,
                  "ordemEntrega": ordemEntrega,
                  "ordemReal": ordemReal,
                  "horaInicial": horaInicial,
                  "horaFinal": horaFinal,
                  "indReceber": indReceber,
                  "sitPagamento": sitPagamento,
                  "sitEntrega": sitEntrega,
                  "obsEntrega": obsEntrega,
                }
            ]
            this.updateFetchNotas(notaAlterar);            
        },

        botaoTipoDados() {
            this.ativoAnalitico = !this.ativoAnalitico;
            this.ativoSintetico = !this.ativoSintetico;
        },

        verificarClasseSituacao(situacaoCarga) {
            if ( situacaoCarga === 1 ) {
                return 'classCorRota';
            } else {
                if ( situacaoCarga === 2 || situacaoCarga === 0 ) {
                    return 'classCorPendente';
                } else {
                    if ( situacaoCarga === 3 ) {
                        return 'classCorEntregue';
                    } else {
                        if ( situacaoCarga === 4 ) {
                            return 'classCorVoltou';
                        }
                    }                    
                }                    
            }
        },

        verificarClasseAtencao(tipoRecebimento) {
            if ( tipoRecebimento === 3 ) {
                return 'classDestacarAtencao'
            } else {
                return 'classNaoDestacarAtencao'
            }
        }
    }, 

    computed: { 
