// import {getByKeyword, getByCategory, getByType, getByState, getByCountry} from '@/store/filtrosPesquisas/filtrosSuporteEntregas.js'
import {getByData, getByCarga, getByPedido, getByNota, getByMotorista} from '@/store/filtrosPesquisas/filtrosSuporteEntregas.js'

const urlApi = "http://SERVIDOR:3000/";
const urlOrigem = ["http://localhost:8080", "http://192.168.15.75:8080", "http://192.168.15.48:8080"];

// PADRÃO VIDEOS JULIUS
const moduloSuporteEntregas = {
    state: {
        notasSuporteEntregas: [],

        filtroDataCarga: 0,

        filtroNumCarga: 0,

        filtroNotaFiscal: 0,

        filtroPedido: 0,

        filtroNomeMotorista: '',

        mensagemAlerta: false,

        // filtros: [
        //     {
        //         dataInicial: '',
        //         dataFinal: '',
        //         cargaInicial: 0,
        //         cargaFinal: 0
        //     }
        // ],
    },

    getters: {
        filtroDataCarga(state) {
            return state.filtroDataCarga;
        },
        filtroNumCarga(state) {
            return state.filtroNumCarga;
        },
        filtroPedido(state) {
            return state.filtroPedido;
        },
        filtroNotaFiscal(state) {
            return state.filtroNotaFiscal;
        },
        filtroNomeMotorista(state) {
            return state.filtroNomeMotorista;
        },

        // todasNotas(state) {
        //     return state.notasSuporteEntregas;
        // },

        todasNotas(state) {
            return getByData(getByCarga(getByPedido(getByNota(getByMotorista(
                state.notasSuporteEntregas, 
                state.filtroNomeMotorista), 
                state.filtroNotaFiscal), 
                state.filtroPedido), 
                state.filtroNumCarga), 
                state.filtroDataCarga);                
        },

        getMensagemAlerta(state) {
            return state.mensagemAlerta;
        }
    },

    actions: {        
        getFetchNotas: ({ commit }, { self }) => {
            let requestOptions = {
                origin: urlOrigem,
                optionSuccessStatus: 200
            };

            fetch(`${urlApi}suporteentregas`, requestOptions).then((resposta) => {
                return resposta.json();
            }).then ((resposta) => {                
                commit('BUSCAR_DADOS_BD', resposta);
                self.filterNotas();
            })
        },

        updateFetchNotas: ({ commit }, notaAlterar) => {
            let requestOptions = {
                method: 'PATCH',
                origin: urlOrigem,
                optionSuccessStatus: 200,
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ notaAlterar }),
            };

            fetch(`${urlApi}updsuporteentregas`, requestOptions )
              .then((resposta) => {
                  console.log('1', resposta)
                return resposta.text();})
              .then ((resposta) => {
                console.log('2', resposta)
                console.log('3',resposta.status)
                if ( resposta === 'ok' || resposta === '' || resposta === ' ' ) {
                    commit('ALTERAR_SITUACAO_SUPORTE_NF', notaAlterar, resposta);                 
                    alert('Alteração realizada com sucesso!')
                } else {
                    alert('ERRO na alteração!')
                }
            })
            .catch((erro) => {
                alert('ERRO! ' + erro);
            })
        },
    },

    mutations: {
        BUSCAR_DADOS_BD: (state, notasFiscais) => {
            state.notasSuporteEntregas = notasFiscais;
        },
    
        ATUALIZAR_DADOS_BD: (state, notasFiscais) => {
            console.log('state:', state, 'notas:', notasFiscais)
        },

        ALTERAR_SITUACAO_SUPORTE_NF: (state, notaAlterar) => {
            for ( var i=0; i<state.notasSuporteEntregas.length; i++ ) {
                if ( state.notasSuporteEntregas[i].codEmpresa === notaAlterar[0].codEmpresa &&
                     state.notasSuporteEntregas[i].numCarga === notaAlterar[0].numCarga &&
                     state.notasSuporteEntregas[i].seqRegistro === notaAlterar[0].seqRegistro ) {

                        console.log('achou aqui', notaAlterar[0].ordemEntrega)

                    state.notasSuporteEntregas[i].ordemEntrega = notaAlterar[0].ordemEntrega;
                    state.notasSuporteEntregas[i].ordemReal = notaAlterar[0].ordemReal;
                    state.notasSuporteEntregas[i].horaInicial = notaAlterar[0].horaInicial;
                    state.notasSuporteEntregas[i].horaFinal = notaAlterar[0].horaFinal;
                    state.notasSuporteEntregas[i].indReceber = notaAlterar[0].indReceber;
                    state.notasSuporteEntregas[i].sitPagamento = notaAlterar[0].sitPagamento;
                    state.notasSuporteEntregas[i].sitEntrega = notaAlterar[0].sitEntrega;
                    state.notasSuporteEntregas[i].obsEntrega = notaAlterar[0].obsEntrega;
                }
            }            
        },

        ALTERAR_SOMENTE_SITUACAO: ( state, notaAlterarSituacao ) => {
            for ( var i=0; i<state.notasSuporteEntregas.length; i++ ) {
                if ( state.notasSuporteEntregas[i].codEmpresa === notaAlterarSituacao[0].codEmpresa &&
                     state.notasSuporteEntregas[i].numCarga === notaAlterarSituacao[0].numCarga &&
                     state.notasSuporteEntregas[i].seqRegistro === notaAlterarSituacao[0].seqRegistro ) {

                    state.notasSuporteEntregas[i].sitEntrega = notaAlterarSituacao[0].sitEntrega;
                }
            }            
        },


        FILTRO_MOTORISTA: ( state, payload ) => {
            state.filtroNomeMotorista = payload;
        },

        FILTRO_DATA_CARGA: ( state, payload ) => {
            state.filtroDataCarga = payload;
        },

        FILTRO_PEDIDO: ( state, payload ) => {
            state.filtroPedido = payload;
        },

        FILTRO_NOTA_FISCAL: ( state, payload ) => {
            state.filtroNotaFiscal = payload;
        },

        FILTRO_NUM_CARGA: ( state, payload ) => {
            state.filtroNumCarga = payload;
        },

        SET_MENSAGEM_ALERTA: ( state, payload ) => {
            state.mensagemAlerta = payload;
        }
    },
}

export default moduloSuporteEntregas;