const moduloGeralMensagem = {
    state: {
        return: {
            mensagemDescricao: '',
            listarMensagem: true,
            tipoMensagem: ''  //erro, ok
        }
    },

    getters: {
        verMensagem(state) {
            return state.mensagemDescricao;
        }
    },

    actions: {        

    },

    mutations: {
        GRAVAR_MENSAGEM: (state, mensagem) => {
            state.mensagemDescricao = mensagem;
        },    
    }
}

export default moduloGeralMensagem;