<template>
    <component-loading :loadingAtivo=listarLoading()></component-loading>
    <w-app>
        <div class="topoFixo">
            <aside>
                <component-menu></component-menu>
            </aside>
            <header>
                <div class="xs12 md12 lg12 headerRight">Quantidade<br><a class="pulse"> {{ retornarTotalPedidos }}</a></div>
            </header>
        </div>
        <main class="grow detalhes">    
            <w-flex class="row"> 
                <component-grid sizeGrid="xs6">   
                    <div class="pedidosAnalisar">                
                        <div class="analisar" v-for="analisar in pedidosAnalisar" :key="analisar.numeroPedido"                     
                            @click="alterarSituacao(analisar.codigoEmpresa, analisar.codigoFilial, analisar.numeroPedido, 'S')">
                                <w-flex class="wrap align-center justify-space-between">
                                    <component-grid sizeGrid="xs12 md4 lg4">
                                        <div class="numero">{{ analisar.numeroPedido }}</div>
                                    </component-grid>

                                    <component-grid sizeGrid="xs12 md2 lg2 mx2">
                                        <div class="mkl" :class="atribuirClasse(parseFloat(analisar.percentualMkl))">
                                            MKL: {{ formatarPercentual(parseFloat(analisar.percentualMkl)) }}%
                                        </div>
                                    </component-grid>

                                    <component-grid sizeGrid="xs12 md4 lg4">
                                        <div class="dados">
                                            <div>{{ analisar.usuarioAprovacao }}</div>
                                            <div>{{ new Date(analisar.dataAprovacao).toLocaleDateString('pt-br', {timeZone: 'UTC'})}}</div>
                                            <div>{{ analisar.horaAprovacao }}</div>
                                        </div>   
                                    </component-grid>

                                    <component-grid sizeGrid="xs12 md1 lg1">
                                        <div>
                                            <i class="fa-solid fa-arrow-right green fa-2x"></i>
                                        </div>                                             
                                    </component-grid>
                                </w-flex>
                        </div>  
                    </div>   
                </component-grid>

                <component-grid sizeGrid="xs6">
                    <div class="pedidosAnalisados">
                        <div class="analisado" v-for="analisado in pedidosAnalisados" :key="analisado.numeroPedido"
                            @click="alterarSituacao(analisado.codigoEmpresa, analisado.codigoFilial, analisado.numeroPedido, 'N')">
                                <w-flex class="wrap align-center justify-space-between">
                                    <component-grid sizeGrid="xs12 md1 lg1">
                                        <div>
                                            <i class="fa-solid fa-arrow-left red fa-2x"></i>
                                        </div>                                             
                                    </component-grid>

                                    <component-grid sizeGrid="xs12 md4 lg4">
                                        <div class="numero">{{ analisado.numeroPedido }}</div>

                                    </component-grid>

                                    <component-grid sizeGrid="xs12 md2 lg2 mx2">
                                        <div class="mkl" :class="atribuirClasse(parseFloat(analisado.percentualMkl))">
                                            MKL: {{ formatarPercentual(parseFloat(analisado.percentualMkl)) }}%
                                        </div>               
                                    </component-grid>

                                    <component-grid sizeGrid="xs12 md4 lg4">
                                        <div class="dados">
                                            <div>{{ analisado.usuarioAprovacao }}</div>
                                            <div>{{ new Date(analisado.dataAprovacao).toLocaleDateString('pt-br', {timeZone: 'UTC'})}}</div>
                                            <div>{{ analisado.horaAprovacao }}</div>
                                        </div>                      
                                    </component-grid>
                                </w-flex>                                    
                        </div>                    
                    </div>
                </component-grid>    
            </w-flex>                
        </main>
        <component-footer></component-footer>
    </w-app>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import ComponentMenu from "@/components/menu/ComponentMenu.vue";
import ComponentGrid from "@/components/geral/ComponentGrid.vue"
import ComponentLoading from "@/components/geral/ComponentLoading.vue";
import ComponentFooter from "@/components/footer/ComponentFooter.vue"
import '@fortawesome/fontawesome-free/css/all.css'
import '@fortawesome/fontawesome-free/js/all.js'

export default {    
    name: 'PagePedidosAprovados',

    components: { ComponentMenu, ComponentGrid, ComponentLoading, ComponentFooter },

    data: () => ({
        selectRadios: 1,
        radios: [
            { label: 'TODOS', value: 0, color: 'gray' },
            { label: 'MKL maior/igual 15%', value: 1, color: 'green' },
            { label: 'MKL menor que 15%', value: 2, color: 'yellow' },
            { label: 'MKL menor que 0%', value: 3, color: 'error' }
        ],

        loadingAtivo: false,

        intervalPedidosAprovados: 0,

        rodandoSetInterval: false,

        abrirOpcaoAtualizar: false
    }), 

    created() {
        this.atualizarAutomatico();
        
        this.$store.dispatch("getFetchPedidos", { self: this });   
    },

    beforeUnmount () {
        this.atualizarAutomatico();
    },

    methods: {
        ...mapActions(["updateFetchPedidos"]),

        filterPedidos() {

        },      
        
        alterarSituacao(empresa, filial, pedido, flag) {   
            const pedidoAlterar = [
                { "codigoEmpresa": empresa,
                  "codigoFilial": filial,
                  "numeroPedido": pedido,
                  "flagConferido": flag
                },            
            ]            
            this.updateFetchPedidos(pedidoAlterar);
        },

        atualizarDadosTela() {
            // console.log("Atualizando...");
            // console.log('interval', this.intervalPedidosAprovados)
            this.$store.dispatch("getFetchPedidos", { self: this });        
        },

        formatarPercentual(percentualMkl) {
            return percentualMkl.toLocaleString('pt-BR', { 
                style: 'decimal', 
                currency: 'BRL', 
                minimumFractionDigits: 2, 
                maximumFractionDigits: 2 
            });
        },

        atribuirClasse(percentual)  { 
            let className = 'mklPositivo';
            if ( percentual < 15 && percentual > 0 ) {
                className = 'mklAtencao';
            } else {
                if ( percentual <= 0 ) {
                    className = 'mklNegativo';
                }
            }
            return className;
        },

        listarLoading() {
            if ( this.loadingAtivo === true ) {
                return 'loading';
            } else {   
                return 'noLoading';
            }
        }, 

        escolherOpcaoAtualizar() {
            this.abrirOpcaoAtualizar = !this.abrirOpcaoAtualizar;
        },

        atualizarAutomatico: function() {
            if ( this.intervalPedidosAprovados === false || this.intervalPedidosAprovados === 0 || this.intervalPedidosAprovados === undefined ) {
                clearInterval(this.intervalPedidosAprovados);

                this.intervalPedidosAprovados = setInterval(() => {
                    this.atualizarDadosTela();
                }, 50000);
                // console.log('apos',this.intervalPedidosAprovados)
            } else {
                clearInterval(this.intervalPedidosAprovados);
            }        
        }            
    },   

    computed: { 
        ...mapGetters(["pedidosAnalisar", "pedidosAnalisados", "retornarTotalPedidos"]),          
    },

    watch: {

    },
}
</script>

