const moduloUsuarioLogado = {
    state: {
        nomeUsuarioLogado: false,

        dadosUsuarioLogado: false,

        grupoUsuarioDiretoria: false,

        grupoUsuario: false,
    },

    getters: {
        getNomeUsuarioLogado(state) {
            return state.nomeUsuarioLogado;
        },
        getGrupoUsuarioDiretoria(state) {
            return state.grupoUsuarioDiretoria;
        },
        getGrupoUsuarioLogado(state) {
            return state.grupoUsuario;
        }
    },

    actions: {        
        getFetchUsuario: ({ commit }, { self }) => {
            commit('BUSCAR_DADOS_BD', self);
        },
    },

    mutations: {
        BUSCAR_DADOS_BD: (state, dadosUsuario) => {
            state.dadosUsuarioLogado = dadosUsuario;
        },        
        
        VERIFICAR_USUARIO_LOGADO: (state, usuario) => {
            state.grupoUsuarioDiretoria = false;
            state.nomeUsuarioLogado = usuario;
        },

        VERIFICAR_GRUPO_USUARIO_LOGADO: (state, grupo) => {
            state.grupoUsuario = false;
            state.grupoUsuario = grupo;
        },    
    },
}

export default moduloUsuarioLogado;